@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;7005&display=swap');

html,
body,
#root,
.app,
.content {
  height:100%;
  width: 100%;
  font-family: 'source';
}

.app{
  display: flex;
  position: relative ;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #e0e0e0;
}

/* handle */
::-webkit-scrollbar-track:hover {
  background: #555;
}


